import document from 'global/document';
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { Carousel } from 'react-responsive-carousel';
import { Row, Col } from 'reactstrap';

// import SVG
import LandingAppSectionPhone from 'assets/images/Landing/Phone.png';
import LandingAppSectionBGMagician from 'assets/images/Landing/Magician.svg';

// import phone carousel images
import PhoneCarousel from 'assets/images/Landing/Phone';
// import appstore and playstore logo
import AppStoreBadge from 'assets/images/app-store-badge.png';
import PlayStoreBadge from 'assets/images/google-play-badge.png';

// import css
import './LandingAppSection.scss';

// Time to delay animation
const delayTime = 1000;

class LandingAppSection extends Component {
  constructor(props) {
    super(props);
    this.renderPhoneCarousel = this.renderPhoneCarousel.bind(this);
    this.addAnimateAppHandler = this.addAnimateAppHandler.bind(this);
  }

  addAnimateAppHandler() {
    const addAniDesc = document.getElementsByName('app-desc');
    const addAniImg = document.getElementsByName('app-img');
    // first animation starts when scroll
    for (let i = 0; i < addAniDesc.length; i++) {
      addAniDesc[i].classList.add('desc-ani');
    }
    // delay for second animation
    setTimeout(() => {
      for (let i = 0; i < addAniImg.length; i++) {
        addAniImg[i].classList.remove('hidden');
        addAniImg[i].classList.add('img-ani');
      }
    }, delayTime);
  }
  renderPhoneCarousel() {
    return _.map(PhoneCarousel.images, (image, index) => (
      <div className="phone-images" key={index}>
        <img src={image.src} alt={image.alt} key={index} />
      </div>
    ));
  }
  render() {
    console.log('RENDER MAGICIAN');

    return (
      <Fragment>
        <Col xs={12} className="section-container-fixed landing-app-section d-flex justify-content-center align-items-center position-relative">
          <Row className="sub-container" noGutters>
            <Col xs={12}>
              <Row className="landing-app">
                <Col
                  md={{ size: 6, order: 1 }}
                  xs={{ size: 10, order: 2 }}
                  style={{ height: '100%' }}
                  className="content"
                >
                  <div className="outer-container set-inner-height">
                    <div
                      className="phone-wrapper"
                      style={{ position: 'relative' }}
                    >
                      <Carousel
                        selectedItem={2}
                        axis="vertical"
                        showThumbs={false}
                        showIndicators={true}
                        showArrows={false}
                        showStatus={false}
                        swipeable={false}
                        infiniteLoop={true}
                        interval={5000}
                        autoPlay={true}
                        className="phone-carousel-container"
                      >
                        {this.renderPhoneCarousel()}
                      </Carousel>
                      <img src={LandingAppSectionPhone} className="landing-app-section-phone" alt="landing-app-section-phone.jpg" />
                      <LazyLoadImage
                        ref={node => this.magician = node}
                        id="magician"
                        className="landing-app-section-magician"
                        alt="landing-app-section-magician.jpg"
                        // height={image.height}
                        src={LandingAppSectionBGMagician}
                        // width={image.width}
                      />
                      {/* <img ref={node => this.magician = node} id="magician" className="landing-app-section-magician" src={LandingAppSectionBGMagician} alt="landing-app-section-magician.jpg" /> */}
                    </div>
                  </div>
                </Col>
                <Col
                  md={{ size: 5, order: 2 }}
                  xs={{ size: 12, order: 1 }}
                  className="col-center download-app-container"
                >
                  <Row className="magic-paragraph">
                    <h2>
                        The <span>Magic</span> is in our app
                    </h2>
                    <p name="app-desc">
                      All your insurance needs at your fingertips. Get covered on
                      the go, manage all your insurance matters in a single app.
                    </p>
                  </Row>
                  <Row className="web-show">
                    <div className="allow-float-left download-now">
                      <p>Download Now</p>
                      <a href="https://itunes.apple.com/sg/app/policypal/id1118511972?mt=8">
                        <img src={AppStoreBadge} alt="appstore.jpg" />
                      </a>
                      <a href="https://play.google.com/store/apps/details?id=com.idvsgjo31w9m05f6raeb3&amp;hl=en">
                        <img src={PlayStoreBadge} alt="playstore.jpg" />
                      </a>
                    </div>
                  </Row>
                </Col>
                <Col
                  xs={{ size: 12, order: 3 }}
                  className="mobile-show"
                >
                  <Row className="download-now-resp">
                    <Col xs={12}>
                      <p>Download Now</p>
                    </Col>
                    <Col xs={6}>
                      <a href="https://itunes.apple.com/sg/app/policypal/id1118511972?mt=8">
                        <img src={AppStoreBadge} alt="appstore.jpg" />
                      </a>
                    </Col>
                    <Col xs={6}>
                      <a href="https://play.google.com/store/apps/details?id=com.idvsgjo31w9m05f6raeb3&amp;hl=en">
                        <img src={PlayStoreBadge} alt="playstore.jpg" />
                      </a>
                    </Col>
                  </Row>
                </Col>
              </Row>
            </Col>
          </Row>
        </Col>
      </Fragment>
    );
  }
}

export default LandingAppSection;
